@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap");

:root {
  //Variables
  --color-white: #ffffff;
  --color-card-white: #fffbf6;
  --color-cream-bg: #f8e9dd;
  --color-med-brown: #92857a;
  --color-dark-brown: #382314;
  --color-cyan: #76b5bc;
  --color-red: #ec755d;

  --style-border-radius: 20px;
  --style-font-xl: 4.8rem;
  --style-font-l: 3.2rem;
  --style-font-body: 1.8rem;
  --style-font-mini: 1.5rem;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;

  @media screen and (max-width: 600px) {
    font-size: 53%;
  }
}

body {
  box-sizing: border-box;
  font-family: "DM Sans", sans-serif;
  background-color: var(--color-cream-bg);
  height: 100vh;
}
