.chart-bar {
  height: 20rem;

  flex: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;

  @media screen and (max-width: 600px) {
    height: 15rem;
  }

  &__group {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    position: relative;
    display: flex;
    flex-direction: column;
  }

  &__fill {
    height: 0%;
    background-color: var(--color-red);
    position: absolute;
    bottom: 0;
    width: 100%;
    border-radius: 5px;
    transition: all 0.2s, height 0.5s;
    animation-timing-function: linear;
    animation-delay: 10s;

    &:hover {
      filter: brightness(140%);

      & .chart-bar__hover-label {
        opacity: 100%;
      }
    }
  }

  &__fill-max {
    background-color: var(--color-cyan);
  }

  &__hover-label {
    display: inline-block;
    position: absolute;
    font-size: var(--style-font-body);
    font-weight: 700;
    background-color: var(--color-dark-brown);
    padding: 0.5rem;
    color: var(--color-white);
    border-radius: 5px;
    top: -2.5rem;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0%;
    transition: all 0.3 s;
  }

  &__label {
    font-size: var(--stylef0nt-mini);
    color: var(--color-med-brown);
  }
}
