.display {
  width: 54rem;
  background-color: var(--color-card-white);
  padding: 2.5rem 3rem;
  border-radius: var(--style-border-radius);
  display: flex;
  flex-direction: column;
  gap: 4rem;

  @media screen and (max-width: 600px) {
    width: 100%;
  }

  &__header {
    font-size: var(--style-font-l);
    color: var(--color-dark-brown);
  }
}
