.values {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;

  &__this-month {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    &--value {
      font-size: var(--style-font-xl);
      color: var(--color-dark-brown);
      font-weight: 700;
      line-height: var(--style-font-xl);
    }
  }

  &__percentage {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;

    &--value {
      font-size: var(--style-font-body);
      color: var(--color-dark-brown);
      font-weight: 700;
    }
  }

  &__text {
    font-size: var(--style-font-body);
    color: var(--color-med-brown);
  }
}
